@import url("https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap");
body {
  background-color: white;
  overflow-y: hidden;
  user-select: none;
}

.main {
  min-height: 100%;
  overflow: hidden;
  position: relative;
  left: -5px;
  top: -10px;
}

.nav__title {
  position: relative;
  font-size: 60px;
  font-weight: 4800;
  left: 18.8%;
  top: 35px;
}

.nav__list {
  list-style: none;
  display: flex;
  flex-direction: row;
  gap: 37.5px;
  position: relative;
  left: 52.7%;
  top: -50px;
}

.nav__list__item {
  color: hsl(26, 7%, 40%);
  font-size: 16px;
  font-family: "Inter", sans-serif;
  transition: 0.3s;
}
.nav__list__item:hover {
  cursor: pointer;
  color: hsl(5, 85%, 63%);
}

.section__image--desktop {
  width: 730px;
  position: relative;
  left: 19%;
}

.section__image--mobile {
  display: none;
}

.section__title {
  font-family: "Inter", sans-serif;
  position: relative;
  font-size: 52px;
  left: 19%;
  top: -5px;
  letter-spacing: 3.1px;
  line-height: 56px;
  font-weight: 800;
}

.section__info {
  font-family: "Inter", sans-serif;
  position: relative;
  color: hsl(26, 7%, 40%);
  left: 39%;
  top: -213px;
  letter-spacing: 0.1px;
  line-height: 26px;
  font-size: 15px;
}

.section__button {
  background-color: hsl(5, 85%, 63%);
  color: black;
  font-family: "Inter", sans-serif;
  position: relative;
  left: 39%;
  top: -198px;
  width: 185px;
  height: 47px;
  border: none;
  font-size: 13.5px;
  font-weight: bold;
  letter-spacing: 5px;
  transition: 0.5s;
}
.section__button:hover {
  background-color: hsl(240, 100%, 5%);
  cursor: pointer;
  color: white;
}

.section__new {
  position: relative;
  background-color: hsl(240, 100%, 5%);
  width: 350px;
  left: 59%;
  top: -735px;
  height: 510px;
}

.new__title {
  color: hsl(35, 77%, 62%);
  font-family: "Inter", sans-serif;
  position: relative;
  top: 28px;
  left: 25px;
  font-size: 40px;
}

.new__subtitle {
  font-family: "Inter", sans-serif;
  position: relative;
  color: white;
  font-size: 20px;
  top: 30px;
  letter-spacing: 0.2px;
  transition: 0.3s;
}
.new__subtitle:hover {
  color: hsl(35, 77%, 62%);
  cursor: pointer;
}

.new__info {
  font-family: "Inter", sans-serif;
  position: relative;
  color: white;
  font-size: 15px;
  top: 22px;
  line-height: 25px;
}

.new__first, .new__second {
  border-bottom: 1px solid hsl(233, 8%, 79%);
  width: 300px;
  white-space: nowrap;
  display: inline-block;
  left: 24px;
  position: relative;
  padding-bottom: 37px;
  top: -18px;
}

.new__second {
  top: -32px;
}

.new__third {
  display: inline-block;
  left: 24px;
  position: relative;
  top: -48px;
}

.footer__first, .footer__third, .footer__second {
  position: relative;
  left: 19%;
  top: -685px;
  width: 350px;
  height: 127px;
}

.first__image, .third__image, .second__image {
  width: 100px;
  float: left;
}

.first__number, .third__number, .second__number {
  font-family: "Inter", sans-serif;
  position: relative;
  font-size: 32px;
  color: hsl(5, 85%, 63%);
  left: 22px;
  letter-spacing: 1px;
}

.first__header, .third__header, .second__header {
  color: black;
  font-family: "Inter", sans-serif;
  position: relative;
  font-size: 17px;
  top: -15px;
  letter-spacing: 0.5px;
  left: 22px;
  font-weight: 800;
  transition: 0.3s;
}
.first__header:hover, .third__header:hover, .second__header:hover {
  cursor: pointer;
  color: hsl(5, 85%, 63%);
}

.first__info, .third__info, .second__info {
  color: hsl(26, 7%, 40%);
  font-family: "Inter", sans-serif;
  position: relative;
  left: 22px;
  top: -16px;
  font-size: 15px;
  line-height: 23px;
}

.footer__second {
  left: 39%;
  top: -835px;
}

.second__header {
  left: 26px;
  top: -14px;
}

.footer__third {
  left: 59%;
  top: -982px;
}

.third__header {
  left: 26px;
  top: -14px;
}

.section__menu {
  display: none;
}

.menu__open {
  display: none;
}

@media screen and (max-width: 375px) {
  .main {
    left: -70px;
    top: -70px;
    width: 115vw;
  }
  .nav__list {
    display: none;
  }
  .button__menu__open {
    display: inline-block;
    width: 60px;
    position: relative;
    top: -350px;
    left: 350px;
    border: none;
  }
  .menu__open {
    display: block;
    width: 60px;
  }
  .section__image--desktop {
    display: none;
  }
  .section__image--mobile {
    display: block;
    position: relative;
    width: 340px;
    left: 80px;
  }
  .section__new {
    top: 0;
  }
  .section__title {
    top: -40px;
    left: 85px;
  }
  .section__menu {
    display: none;
    position: fixed;
    top: 15px;
    left: 165px;
    background-color: white;
    width: 220px;
    height: 100%;
    visibility: visible;
  }
  .menu__close {
    position: relative;
    left: 170px;
  }
  .menu__list {
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 25px;
    position: relative;
    top: 35px;
    left: -15px;
  }
  .menu__list__item {
    font-size: 18px;
    font-family: "Inter", sans-serif;
    transition: 0.3s;
  }
  .button__menu__close {
    display: inline-block;
    width: 60px;
    position: relative;
    top: 5px;
    left: -20px;
    border: none;
  }
  .section__info {
    top: -60px;
    left: 85px;
  }
  .section__button {
    top: -60px;
    left: 85px;
  }
  .footer__first, .footer__second, .footer__third {
    top: -20px;
    left: 85px;
  }
  .section__new {
    top: -50px;
    left: 75px;
  }
}

