@import url('https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap');


$softOrange: hsl(35, 77%, 62%);
$softRed: hsl(5, 85%, 63%);
$offWhite: hsl(36, 100%, 99%);
$grayishBlue: hsl(233, 8%, 79%);
$darkGrayishBlue: hsl(236, 13%, 42%);
$veryDarkBlue: hsl(240, 100%, 5%);



body {
  background-color: white;
  overflow-y: hidden;
  user-select: none;
}

.main {
  min-height: 100%;
  overflow: hidden;
  position: relative;
  left: -5px;
  top: -10px;
}

.nav__title {
  position: relative;
  font-size: 60px;
  font-weight: 4800;
  left: 18.8%;
  top: 35px;
}

.nav__list {
  list-style: none;
  display: flex;
  flex-direction: row;
  gap: 37.5px;
  position: relative;
  left: 52.7%;
  top: -50px;
}

.nav__list__item {
  color: hsl(26, 7%, 40%);
  font-size: 16px;
  font-family: "Inter", sans-serif;
  transition: .3s;

  &:hover {
    cursor: pointer;
    color: $softRed;
  }
}

.section__image--desktop {
  width: 730px;
  position: relative;
  left: 19%;
}

.section__image--mobile {
  display: none;
}


.section__title {
  font-family: "Inter", sans-serif;
  position: relative;
  font-size: 52px;
  left: 19%;
  top: -5px;
  letter-spacing: 3.1px;
  line-height: 56px;
  font-weight: 800;
}

.section__info {
  font-family: "Inter", sans-serif;
  position: relative;
  color: hsl(26, 7%, 40%);
  left: 39%;
  top: -213px;
  letter-spacing: .1px;
  line-height: 26px;
  font-size: 15px;
}

.section__button {
  background-color: $softRed;
  color: black;
  font-family: "Inter", sans-serif;
  position: relative;
  left: 39%;
  top: -198px;
  width: 185px;
  height: 47px;
  border: none;
  font-size: 13.5px;
  font-weight: bold;
  letter-spacing: 5px;
  transition: .5s;


  &:hover {
    background-color: $veryDarkBlue;
    cursor: pointer;
    color: white;
  }
}

.section__new {
  position: relative;
  background-color: $veryDarkBlue;
  width: 350px;
  left: 59%;
  top: -735px;
  height: 510px;
}


.new__title {
  color: $softOrange;
  font-family: "Inter", sans-serif;
  position: relative;
  top: 28px;
  left: 25px;
  font-size: 40px;
}

.new__subtitle {
  font-family: "Inter", sans-serif;
  position: relative;
  color: white;
  font-size: 20px;
  top: 30px;
  letter-spacing: .2px;
  transition: .3s;


  &:hover {
    color: $softOrange;
    cursor: pointer;
  }
}

.new__info {
  font-family: "Inter", sans-serif;
  position: relative;
  color: white;
  font-size: 15px;
  top: 22px;
  line-height: 25px;
}

.new__first {
  border-bottom: 1px solid $grayishBlue;
  width: 300px;
  white-space: nowrap;
  display: inline-block;
  left: 24px;
  position: relative;
  padding-bottom: 37px;
  top: -18px;
}

.new__second {
  @extend .new__first;
  top: -32px;

}

.new__third {
  display: inline-block;
  left: 24px;
  position: relative;
  top: -48px;
}



.footer__first {
  position: relative;
  left: 19%;
  top: -685px;
  width: 350px;
  height: 127px;
}

.first__image {
  width: 100px;
  float: left;
}

.first__number {
  font-family: "Inter", sans-serif;
  position: relative;
  font-size: 32px;
  color: $softRed;
  left: 22px;
  letter-spacing: 1px;
}

.first__header {
  color: black;
  font-family: "Inter", sans-serif;
  position: relative;
  font-size: 17px;
  top: -15px;
  letter-spacing: .5px;
  left: 22px;
  font-weight: 800;
  transition: .3s;

  &:hover {
    cursor: pointer;
    color: $softRed;
  }
}

.first__info {
  color: hsl(26, 7%, 40%);
  font-family: "Inter", sans-serif;
  position: relative;
  left: 22px;
  top: -16px;
  font-size: 15px;
  line-height: 23px;
}

.footer__second {
  @extend .footer__first;
  left: 39%;
  top: -835px;
}

.second__image {
  @extend .first__image;
}

.second__number {
  @extend .first__number;
}

.second__header {
  @extend .first__header;
  left: 26px;
  top: -14px;
}

.second__info {
  @extend .first__info;
}


.footer__third {
  @extend .footer__first;
  left: 59%;
  top: -982px;
}

.third__image {
  @extend .first__image;
}

.third__number {
  @extend .first__number;
}

.third__header {
  @extend .first__header;
  left: 26px;
  top: -14px;
}

.third__info {
  @extend .first__info;
}


.section__menu {
  display: none;
}

.menu__open {
  display: none;
}


@media screen and (max-width: 375px) {
  .main {
    left: -70px;
    top: -70px;
    width: 115vw;
  }

  .nav__list {
    display: none;
  }

  .button__menu__open {
    display: inline-block;
    width: 60px;
    position: relative;
    top: -350px;
    left: 350px;
    border: none;
  }

  .menu__open {
    display: block;
    width: 60px;
  }

  .section__image--desktop {
    display: none;
  }

  .section__image--mobile {
    display: block;
    position: relative;
    width: 340px;
    left: 80px;
  }

  .section__new {
    top: 0;
  }


  .section__title {
    top: -40px;
    left: 85px;
  }


  .section__menu {
    display: none;
    position: fixed;
    top: 15px;
    left: 165px;
    background-color: white;
    width: 220px;
    height: 100%;
    visibility: visible;
  }

  .menu__close {
    position: relative;
    left: 170px;
  }

  .menu__list {
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 25px;
    position: relative;
    top: 35px;
    left: -15px;
  }

  .menu__list__item {
    font-size: 18px;
    font-family: "Inter", sans-serif;
    transition: .3s;
  }

  .button__menu__close {
    display: inline-block;
    width: 60px;
    position: relative;
    top: 5px;
    left: -20px;
    border: none;
  }


  .section__info {
    top: -60px;
    left: 85px;
  }

  .section__button {
    top: -60px;
    left: 85px;
  }

  .footer__first {
    top: -20px;
    left: 85px;
  }

  .section__new {
    top: -50px;
    left: 75px;
  }
}